export const messages = {
  hkko: `2년이 빠르게 지나간 것 같네요. 잘난 것도 없는 저를 연구실에 받아주시고 나무라는 것 없이 믿고 봐주신 점 깊은 감사드립니다. 코로나가 장기화 되면서 교수님 얼굴도 뵙지 못하고 스승의 날인데도 따로 식사 하지 못함에 아쉽습니다.  어느덧 졸업을 할 시점이 되었네요. 이후에도 교수님이 자랑스러워 하실 만한 학생으로 거듭나기 위해 열심히 노력하는 인생을 살겠습니다. 스승의 은혜에 감사드립니다. 항상 건강하세요.`,
  ytkim: `4년 반 동안의 연구실 생활이 인생에서 정말 여러모로 의미있었던 기간이었습니다. 
아직 마무리할 것들이 많이 남긴 했지만, 지금까지는 즐거웠고 재미있었습니다. :-) 
남은 연구 정리 잘해서 의미있는 마무리 하도록 하겠습니다. 
교수님 가족 모두 건강하시길 바라겠습니다!`,
  jykim: `교수님, 부족한 저를 연구실에 받아주시고 지도해주셔서 감사합니다. 항상 인자하게 모두를 지도하시는 모습을 보면서 연구 내적으로도, 외적으로도 많은 것을 배우고 있습니다. 다른 사람들도 써뒀지만, 코로나가 장기화되면서 교수님과 연구실 구성원들이 함께하는 자리가 없어진 것이 아쉽습니다. 조만간 코로나가 종식되어 모두가 함께할 수 있는 자리가 생겼으면 좋겠습니다. 항상 건강하시고 감사합니다.   `,
  shpark: `안녕하세요 교수님! 연구실에 막 들어온 신입생인것만 같은데 벌써 1년도 넘는 시간이 흘렀습니다. 항상 부족한 저에게 뜻깊은 말씀과 지도해 주셔서 많은 인사이트를 얻을 수 있었습니다. 앞으로도 교수님의 정성어린 지도를 바탕으로 훌륭한 연구자가 될 수 있도록 노력하겠습니다. 공교롭게도 제가 입학한 이후 코로나가 발발하여 교수님을 비롯해 다른 연구실 구성원과 활발히 소통할 수 있는 시간이 많지 않아 아쉬웠는데, 하루빨리 코로나 사태가 종식되어 즐거운 추억 또한 만들어갈 수 있는 자리가 생기면 좋겠습니다. 항상 감사드립니다!`,
  sbpark: `연구실에 들어온지 엊그제 같은데 벌써 3개월 정도가 지났습니다. 신경써주신 덕분에 많은 것을 배우고 있고 연구실 구성원분들과 함께
행복한 시간을 보내고 있습니다. 특히 교수님 덕분에 오랜 회사생활로 생겼던 고정관념이 많이 변화하고 있어 제 인생이 큰 전환점이 되고 있습니다. 기대하시는 만큼 좋은 연구 성과 낼 수 있도록 항상 최선을 다 하겠습니다. 항상 건강하세요!`,
  esbaek: `안녕하세요! 교수님, 은수입니다. 어느덧 연구실에 입학한지 일년이 지났고 또 이렇게 스승의 날을 빌어 감사한 마음을 수줍게 전합니다. 최근에 항상 걱정을 끼쳐드리는 아이였던거 같아 죄송한 마음과 동시에 그럼에도 불구하고 보듬어 지도해주신 교수님께 항상 감사한 마음을 늘 잊지 않고 있습니다. 교수님께서 은혜를 베풀어주신 만큼, 올 해는 꼭 성장한 모습으로 보답하고 싶습니다. 항상 몸조리 잘하시어 건강하시길 바라며 얼른 코로나가 종식되어 연구실 행사도 함께할 수 있는 날이 오기를 기다리고 있겠습니다:)`,
  bok: `오랜 기간동안 여러모로 모자른 저를 이끌어주시고 도와주셔서 감사합니다. 덕분에 하루하루 조금씩 발전해나가고 있는 것 같습니다. 앞으로도 열심히 연구하고 좋은 성과를 내서 얼른 졸업하겠습니다. 작년에 이어서 스승의 날 행사를 제대로 하지 못해서 아쉽네요. 내년엔 꼭 모두가 모여서 만날 수 있었으면 좋겠습니다. 감사합니다.`,
  dhshin: `돌아보면 연구실 생활을 하면서 저에게는 학업 뿐 아니라 결혼을 하고 자녀들을 얻는 등 많은 일들이 있었습니다. 그때마다 교수님께서 여러 가지로 신경써주신 덕분에 제가 그런 중요한 순간들을 잘 보낼 수 있었던 것 같습니다. 매번 제가 직접 표현을 드리진 못했던 것 같지만 항상 교수님께 감사하는 마음을 갖고 있습니다. 교수님께 부끄럽지 않은 제자가 되도록 저도 열심히 노력하겠습니다. 감사합니다.`,
  dtan: `안녕하세요, 교수님. 코로나19 상황의 장기화로 많이 힘드시겠지만 항상 건강하시기 바랍니다. 제가 박사과정이 되었다는 것이 아직도 실감이 안 나네요. 연구실에 들어와서 2년 동안 한 것도 거의 없고 교수님을 자주 뵙지도 못한 것 같아서 저 자신에 대해 부끄럽게 생각하고 있습니다. 하지만 저를 믿고 기다려주시는 교수님 덕분에 제가 연구를 지속할 힘이 나는 것 같습니다. 코스웍이 마무리되는 대로 제 연구에 박차를 가하고 교수님과의 미팅도 자주 잡을 수 있도록 노력해보겠습니다! 감사합니다.`,
  sban: `교수님 덕분에 하루 하루를 즐겁게 대학원 생활을 할 수 있어 감사합니다.

교수님과 이야기 나눌 수 있는 면담과 랩세미나에서 어떤 날에는 연구의 즐거움을, 어떤 날에는 연구의 어려움을 들으며 마냥 배우는 게 즐겁기만 합니다.  아직 부족한 신입생이지만 앞으로 더 멋진 연구로 교수님에게 자랑스러운 제자가 되고 싶습니다. 연구자의 길을 교수님 밑에서 시작할 수 있어 행복합니다. 

더 자주 뵐 수 있으면 좋겠습니다! 항상 감사합니다 ❤️`,
  bjlee: `언제나 교수님께는 죄송한 마음과 감사한 마음 뿐입니다.
  항상 부족한 모습만 보여드리고 있네요.
  모자란 사람 포기하지 않고 지도해주시는 마음에 언젠가는 보답할 수 있기를 바랄 뿐입니다.
  정말이지 항상 감사드리고 있습니다.`,
  ylchang: `안녕하세요 교수님! 먼저 연구실에 입학한지 많은 시간이 지났는데 아직까지 못나게 남아있어 항상 정말 죄송하고 감사한 마음입니다! 그럼에도 불구하고 늘 이해해주시고 격려해주셔서 정말 감사하다는 말씀을 드리고 싶습니다. 남은시간 교수님을 더욱 자주 찾아봬야 하는데 항상 프로그래스가 부족한것 같아 그러지 못하는점 고쳐 앞으로 더욱 자주 찾아뵙고 열심히해서 졸업할때에는 부끄럽지 않은 제자가 될 수 있도록 정진하도록 하겠습니다. 항상 감사합니다!`,
  hj: `교수님 안녕하세요 벌써 연구실에서 지낸지도 1년이 넘는 시간이 흘렀고 입학한지도  한학기가 넘는 시간이 흘렀네요.   길면 길고 짧다면 짧은 시간이었지만 교수님의 가르침 덕에 별 탈 없이 계속해서 연구실 생활 해 나가는 것 같습니다. 코로나가 끝나면 이제 다 같이 모여서 뭔가를 하고 더 재미있게 시간을 보낼 수 있을 거라 기대 중입니다.   앞으로도 계속해서 즐겁게 공부하고 연구하며 더 좋은 제자가 될 수 있도록 노력하겠습니다. 매번 감사합니다`,
  swjung: `스승의날 맞아 인사드립니다. 교수님께서 항상 신경 써주시고 지도해주셔서 의미있고 즐거운 대학원 생활 보낼수 있는 것 같습니다. 교수님 말씀대로 다음에는 꼭 풀 패이퍼를 써보도록 하겠습니다. 다시 한번 감사드립니다. 내년에는 꼭 코로나가 끝나서 오프라인에서 다 같이 모일 수 있게 되면 좋겠네요. 교수님도 코로나 조심하시고 건강하세요~`,
  archo: `안녕하세요 교수님. 코로나 때문에 아직까지도 대면할 기회는 잘 없었지만 온라인으로나 대면으로 뵐 수 있을때마다 많은것을 알려주셔서 감사합니다. 대학원 입학전부터 부족한 점에 대해 많이 고민하였는데 연구실에 들어와서 교수님과 연구실 분들 덕분에 조금씩 즐겁게 배우고 있습니다. 앞으로도 언제나 최선을 다하고 열심히 지내도록 하겠습니다. 입학 이후로 다같이 모일 수 있는 자리가 가끔있는 세미나 외에는 없어서 많이 아쉽지만 빠른 시일내에 다 같이 뵐 수 있는 자리가 생겼으면 좋겠습니다! 건강조심하세요 :)`,
  krchoe: `교수님 제가 이 연구실에 들어온 지 벌써 3년하고도 반년이 지났습니다. 아직 미숙하지만 그래도 과거를 돌아보면 교수님 덕분에 조금씩 성장해온 것 같아요. 스승의 은혜에 감사드리며 올해는 랩장으로서 연구실을 잘 보필하겠습니다. 잘 부탁드린다는 의미에서 HCI로 삼행시 지어보겠습니다.

(에)브리데이 저는 교수님 덕분에
(이) 연구실에서 행복하게 지냅니다
(치)킨집에서 다 같이 세미나 하고 엠티하던
(시)절이 그립습니다 ㅜㅜ
(아)마 올해도 대면 행사는 어렵겠지만
(이)듬해에는 엠티든 행사든 꼭 가고싶네요...`,
  ghhan: `5월이 되어 각종 행사가 많아지니 새삼 코로나로 인해 삶이 많이 바뀌었다는 생각이 듭니다.

사은회에서 졸업한 선후배들과 다같이 모여서 밀린 얘기도 나누고, 교수님께 감사의 마음을 담은 이벤트를 했던 기억도 점점 흐려지는 것 같아 아쉽네요.

형식은 다르지만 평소에 감사했던 마음을 담아 짧은 글을 남깁니다. 

교수님, 부족한 제게 좋은 지도해주셔서 항상 감사드립니다. 
수상한 시국에 건강에 별고 없으시길 바라고, 내년에는 구성원들이 다같이 모여 기념할 수 있는 스승의 날이 되면 좋겠습니다.`,
  shlee: `안녕하세요 교수님.  아직은 연구실에 인턴으로 들어와있지만 교수님의 화이팅 한마디에 힘내며 즐거운 인턴생활 하고있습니다. 감사합니다. 
아직은 많이 부족하지만 많이 배우면서 열심히 지내도록 하겠습니다. 
입학하고 졸업할때까지 잘부탁드립니다! 항상 건강하시고 감사합니다.`,
}

export const message2 = {
  김원재:	`올해도 코로나가 끝나지 않아서 스승의날 모임을 못가지게 되어서 안타깝습니다 ㅠㅠ. 하루 빨리 정상화가 되어서 직접 만나 그동안의 일들을 나눌 수 있는 자리를 가질 수 있기를 바랍니다. 교수님, 연구실 모든 구성원 분들 건강하시고 힘든 시기에도 좋은 연구 하시길 바랍니다.`,
  이형민:	`교수님, 코로나 때문에 뵙지 못하고 이렇게 인사드리게 되네요. 항상 건강하시고 행복하세요! 랩장님도 연구 바쁘신 와중에 이렇게 기획 해주셔서 고마워요 :)`,
  송현주:	`스승의 날을 맞아 교수님께 감사한 마음을 온라인으로 보내드립니다. 사회에서 여러 가지 경험들을 쌓으면서, 교수님께서 보내주셨던 따뜻한 격려와 깊은 성찰이 담긴 가르침들이 많이 떠오르곤 합니다. 항상 건강하시고, 곧 오프라인에서도 자주 찾아뵐 수 있게 되기를 기원하겠습니다!`,
  김보은:	`교수님, 안녕하세요? 잘 지내시죠? 코로나때문에 이렇게 몇년이나 못 찾아뵈어 죄송한 마음 뿐입니다 ㅜㅜ 항상 제자들을 위해 노력해 주시고, 아낌없이 지원해 주신 덕분에 저희가 이렇게 잘(?) 지내지 않을까 싶습니다ㅎㅎ 저도 잘 지내고 있어요!! ^^ 더 좋은 모습으로 보답할 수 있도록 노력하겠습니다! 교수님, 늘 감사드리며 건강하세요!! 내년 스승의 날에는 꼭 뵐 수 있었으면 좋겠어요 ㅜㅜ`,
  황정인:	`안녕하세요 교수님, 코로나 시국이 길어져 2년째 온라인으로 인사드립니다ㅠㅠ. 저는 몇 달 전에 병특도 마치고 건강히 지내고 있습니다. 코로나 조심하시고 항상 건강하세요!`,
  정대경:	`교수님 안녕하세요, 요새 드는 생각이 교수님께 배울 수 있어서 정말 운이 좋았던 것 같습니다. 다시 돌아가더라도 우리 연구실로 갈 것 같습니다 (교수님께서 안 받아주시려나요..?ㅠ). 항상 건강하시고 다음에 뵙겠습니다!`,
  박헌진:	`교수님 안녕하세요. 헌진입니다.
  직접 뵙고 인사드리지는 못하지만, 이렇게나마 감사한 마음을 전달 드릴 수 있어 다행이네요.
  문득 되돌아보면 비록 좋은 학생은 아니었을지라도, 관악에서 교수님의 지도를 받으며 지낸 시간들은 즐거웠습니다.
  날씨도 화창한 요즘 날씨처럼 처럼 항상 가볍고 화창한 하루 보내시길 바라겠습니다.
  교수님 감사합니다.`,
  박예슬: `교수님 🥰 안녕하세요
  생각보다 코로나 시기가 길어지고 있는 와중에 건강히 잘 지내고 계신지요. 뵌지가 오래 되었네요...
  언제나 존재감 없이 조용한 저이지만 교수님을 종종 떠올리고 항상 감사드리고 있답니다. 
  어서 다시 다들 자유롭게 돌아다닐 수 있는 때가 되어서 랩 사람들 다 같이 모일 날을 기다리겠습니다. 
  교수님도 가족분들도 그때까지 다들 몸 건강하게 즐겁게 지내시기 바래요. 
  감사합니다! 건강하세요!!🤗🤗`,
  유승훈: `교수님, 큰 가르침 주심에 감사드리며 늘 행복하시고 건강하시길 바랍니다.`,
  김민지: `교수님 코(로나)시국에 건강 잘 챙기고 계시죠? 스승의 날 모임 없는게 너무 아쉽네요... 대학원 시절이 참 행복했고 생각이 많이 납니다ㅎㅎㅎㅎ 네이버 근처에 오시면 꼭 연락주세요 (재택이라 미리 말씀주시면 출근하겠습니다 :D)`,
  카일: `교수님 그립습니다~`,
  이세희: `교수님, 안녕하세요. 보스턴에서 연락드립니다! 이곳 먼 타지에서 지내다 보면 교수님의 지도를 받으며 연구하던 때가 자주 생각나요. 요즘 뉴스를 보면 시국이 혼란스럽던데, 평안하시고 건강하시길 바라요. 아직 계획된 건 없지만, 귀국하면 교수님께 찾아뵙겠습니다! 감사합니다.`,
  김준회: `안녕하세요, 교수님. 작년에 졸업하고나서 스승의 날에 한 번이라도 찾아갈 기회가 있으면 좋았겠지만 코로나때문에 기회가 잘 오지 않네요. 연구실은 요즘도 과제 등으로 계속 바쁘다고 들었습니다. 너무 과로하지 마시고 건강 꼭 챙기세요! 코로나가 좀 정리되면 맛있는 것 싸들고 찾아봽겠습니다!`,
  김영호: `졸업하고 미국에 나와서도 교수님이 여전히 저의 참 스승임을 느낍니다. 교수님의 가르침에 늘 감사드립니다.`,
  이성원: `서진욱 교수님, 요새 자주 못 찾아봬서 죄송합니다. 종종 대학원 휴학하려던 때 교수님께서 해주신 말씀이 떠올라서 여기저기 인용하고 있습니다. 항상 저희 제자들에게 좋은 가르침 주시고 응원해주셔서 정말 감사드립니다. 곧 찾아 뵐게요, 건강하세요!`,
  김이은: `안녕하세요 교수님. 코로나로 직접 얼굴뵙고 인사드리지 못해 아쉽습니다. 연구실에 있는 동안 늘 감사했습니다. 항상 건강하시고 행복하세요!`,
  조재민: `교수님, 스승의 날 축하드립니다. 이전에 얼마나 고생이 많으셨을지 몸으로 느끼고 있습니다. 고생 많으셨습니다. 감사합니다.`,
  이수연: `스승의 날을 맞아 오랜만에 인사드립니다! 항상 감사하고 존경합니다. 늘 건강하세요!`,
  최고은: `항상 감사드립니다. 건강하세요~ 곧 뵐 날을 기다립니다!!`,
  고재희: `안녕하세요 교수님. 고재희입니다. 
  
  그동안 평소에 따로 찾아 뵙지도 못하고 일 년 중 스승의날에만 뵈었는데 이마저도 코로나 때문에 이렇게 롤링페이퍼로만 소식을 전하게 되었습니다. 교수님께서는 무탈하게 잘 지내고 계셨는지 모르겠습니다. 
  
  제가 HCI 랩에서 교수님 지도를 받으며 석사 과정을 시작했던게 2011년이니 벌써 10년이 지났네요. 제가 좋은 교수님들 만나 많이 배우고 또 연구실에서 선배들과 즐겁게 생활할 수 있었던 게 아무에게나 흔히 찾아오는 행운이 아니라고 생각하고 교수님께 항상 감사한 마음을 가지고 살고 있습니다. 
  
  저는 최근에 딸 아빠가 되었습니다. 지난주에 100일이 되었네요. 어서 시국이 잠잠해지고 아기도 조금 더 크면 아이 데리고 교수님께 인사 전하러 가겠습니다.
  
  항상 건강 조심하세요.
  감사합니다.`,
  박관모: `안녕하세요 교수님, 저는 늘 감사하고도 죄송한 마음이 큽니다. 내년엔 스승의날 모임이 정상적으로 진행될수 있으면 좋겠네요. 그때까지 항상 행복하고 건강하시길 바랍니다.`,
  고봉경: `교수님 고봉경입니다.
  비록 길지않은 시간동안 교수님의 지도를 받고 연구실 생활을 마쳤지만 그 동안 배웠던 연구에 대한 자세는 이후 회사생활을 할 때 많은 도움이 되었습니다.
  질병 핑계로 찾아뵙지 못해 죄송합니다. 시국이 좀 안정되면 좋은 소식과 함께 뵙겠습니다.
  그동안 강녕하시길 바랍니다.`,
}